/**
 * @description cookie
 * @param {Object} json cookie
 * @param {Number} days 
 */
function setCookie(json, days) {
  // 
  let data = new Date(
    new Date().getTime() + days * 24 * 60 * 60 * 1000
  ).toUTCString();

  for (var key in json) {
    document.cookie = key + "=" + json[key] + "; expires=" + data;
  }
}

/** 
 * @description cookie
 * @param {String} name cookiekey
 */
function getCookie(name) {
  var arr = document.cookie.match(new RegExp("(^| )" + name + "=([^;]*)(;|$)"));
  if (arr != null) {
    return unescape(arr[2])
  } else {
    return null
  }
}

/** 
 * @description cookie
 * @param {String} name cookiekey
 */
function clearCookie(name) {
  let json = {};
  json[name] = '';
  setCookie(json, -1)
}

export default {
  setCookie,
  getCookie,
  clearCookie
}