<template>
  <div class="first">
    <div class="second">
      <img src="../assets/login_computer.png" alt="">
      <h3 class="sysName"><br>ChatAAA</h3>
      <div class="text">
        <h3></h3>
        <input v-model="mail_account" type="text" class="inp inp1" placeholder="">
        <input v-model="code_input" type="text" class="inp inp2" placeholder="">
        <button @click="sendCode()"  :disabled="isWaiting" class="sendCode">{{ isWaiting ? `${remainingTime}s` : '' }}</button>
        <input v-model="password" type="password" class="inp inp3" placeholder="10-12">
        <input v-model="password2" type="password" class="inp inp4" placeholder="">
        <br>
        <button @click="chess()" class="chess"></button>
        <br>
        <div class="backToLogin" @click="back()"></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'FindPassword',
  data () {
    return {
      mail_account: '',
      mail_check: '', // 
      password:'',
      password2:'',
      code:'',
      code_input:'',
      validPassword: true,
      isWaiting: false, // false
      remainingTime: 60 // 60
    }
  },

  created () {
    document.title = '-chataaa'
    if (localStorage.getItem('mail_check')) {
      this.mail_check = localStorage.getItem('mail_check')
      this.mail_account = this.mail_check
    }
    this.code = localStorage.getItem('code')
    const endTime = localStorage.getItem('endTime2') // 
    if (endTime && endTime > Date.now()) { // 
      this.remainingTime = Math.ceil((endTime - Date.now()) / 1000) // 
      this.isWaiting = true
      this.startTimer() // 
    }
  },

  methods: {
    checkPassword() {
      const regex = /^(?=.*[a-zA-Z])(?=.*\d)(?!.*\s).{10,12}$/;
      this.validPassword = regex.test(this.password);
    },
    async sendCode () {
      if (this.mail_account.trim() === '') {
        alert(``)
        return
      }
      if (!this.isWaiting) { // 
        const res = await axios.post('http://8.134.178.190:5000/login_system/retrieve_verification_code', {
          mail_account: this.mail_account,
        })
        this.mail_check = res.data.mail_account
        localStorage.setItem('mail_check', this.mail_account)
        localStorage.setItem('code', res.data.verification_code)
        if (res.data.code === '400') {
          alert('')
          return
        }
        if (res.data.code === '401') {
          alert(``)
          return
        }
        if (res.data.code === '400') {
          alert(``)
          return
        }
        this.code = res.data.verification_code

        const endTime = Date.now() + 60000 // 60
        localStorage.setItem('endTime2', endTime) // 
        //
        this.isWaiting = true
        this.startTimer()
      }
    },
    startTimer () {
      const timer = setInterval(() => {
        if (this.remainingTime > 0) {
          this.isWaiting = true
          this.remainingTime--
        } else {
          this.isWaiting = false
          this.remainingTime = 60
          localStorage.removeItem('endTime2')
          clearInterval(timer)
        }
      }, 1000);
    },
    async chess () {
      this.checkPassword()
      if (this.mail_account.trim() === '') {
        alert('')
        return
      }
      if (this.code_input.trim() === '') {
        alert('')
        return
      }
      if (this.password.trim() === '') {
        alert(``)
        return
      }
      if (this.password2.trim() === '') {
        alert(``)
        return
      }
      if (this.mail_check !== this.mail_account) {
        alert(``)
        console.log(this.mail_account)
        console.log(this.mail_check)
        return
      }
      if (this.code !== this.code_input) {
        alert(``)
        return
      } else 
      if (this.validPassword === false) {
        alert(``)
        return
      } else if (this.password !== this.password2) {
        alert(``)
        return
      }
      const res = await axios.post('http://8.134.178.190:5000/login_system/retrieve_password', {
        mail_account: this.mail_account,
        password: this.password
      })
      if (res.data.code === '400') {
          alert('')
          return
        }
      alert(``)
      localStorage.removeItem('mail_check')
      localStorage.removeItem('code')
      this.mail_check = ''
      this.$router.push('/login_system')
    },
    back () {
      this.$router.push('/login_system')
      localStorage.removeItem('mail_check')
      localStorage.removeItem('code')
    }
  }
}
</script>

<style scoped>
.first {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 985px;
  background-image: url(../assets/login.jpg);
  background-size: cover;
}

.first .second {
  width: 1050px;
  height: 500px;
  position: relative;
  display: flex;
}

.second img {
  border-radius: 10px 0 0 10px;
  width: 600px;
  height: 500px;
  opacity: 0.5;
}

.second .sysName {
  position: absolute;
  left: 40px;
  top: 50px;
  font-size: 40px;
  color: #fff;
}

.second .text {
  position: relative;
  width: 450px;
  height: 500px;
  background-color: #fff;
  border-radius: 0 10px 10px 0;
}

.text h3 {
  text-align: center;
  padding: 40px 40px 70px 40px;
  color: #3A62D7;
  font-size: 30px;
}

.second .text .inp {
  display: inline-block;
  position: absolute;
  width: 340px;
  height: 30px;
  margin: 50px;
  padding: 5px;
  border: none;
  border-bottom: 1px solid #DCDCDC;
}

.inp:focus {
  outline: none;
}

.inp1 {
  position: absolute;
  left: 20px;
  top: 100px;
}

.inp2 {
  position: absolute;
  left: 20px;
  top: 145px;
}

.inp3 {
  position: absolute;
  left: 20px;
  top: 190px;
}

.inp4 {
  position: absolute;
  left: 20px;
  top: 235px;
}

.sendCode {
  position: absolute;
  width: 90px;
  height: 35px;
  background-color: #1990FF;
  color: #fff;
  font-size: 12px;
  border-radius: 30px;
  text-align: center;
  line-height: 35px;
  top: 190px;
  right: 40px;
  cursor: pointer;
}

.sendCode:hover {
  width: 93px;
  height: 37px;
  text-align: center;
  line-height: 37px;
  opacity: 0.7;
  transition: 0.2s;
}

.text .forget {
  position: absolute;
  right: 80px;
  top: 300px;
  font-size: 12px;
  color: #3A62D7;
  font-weight: 700;
  cursor: pointer;
}

.text .chess {
  position: absolute;
  display: block;
  width: 340px;
  height: 45px;
  background-color: #3A62D7;
  text-align: center;
  line-height: 45px;
  color: #fff;
  font-size: 16px;
  margin: 0 auto;
  top: 350px;
  left: 55px;
  cursor: pointer;
}

.text .chess:hover {
  border-radius: 10px;
  width: 345px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  opacity: 0.7;
  transition: 0.2s;
}

.text .backToLogin {
  position: absolute;
  display: block;
  width: 340px;
  height: 45px;
  background-color: #3A62D7;
  text-align: center;
  line-height: 45px;
  color: #fff;
  font-size: 16px;
  margin: 0 auto;
  top: 420px;
  left: 55px;
  cursor: pointer;
}

.text .backToLogin:hover {
  border-radius: 10px;
  width: 345px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  opacity: 0.7;
  transition: 0.2s;
}
</style>